import React from 'react';
import { withPrefix, graphql } from 'gatsby';
import Img from "gatsby-image"
import Layout from '../layouts/index';
import './index.less';

export const query = graphql`
  query {
    image1: file(relativePath: { eq: "mourning/photo4.jpg" }) {
      childImageSharp {
        fixed(width: 200) {   
          ...GatsbyImageSharpFixed
        }
      }
    }

    image2: file(relativePath: { eq: "mourning/photo5.jpg" }) {
      childImageSharp {
        fixed(width: 200) {
          ...GatsbyImageSharpFixed
        }
      }
    }

    image3: file(relativePath: { eq: "mourning/photo3.jpg" }) {
      childImageSharp {
        fixed(width: 300) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
`

export default ({ data }) => {
  console.log(data);
  return (
    <Layout>
      <div className="article">
        <br/>
        <br/>
        <h1 className="center">沉痛悼念我的父亲</h1>
        <br/>
        <br/>
        <br/>
        {/* <img src={withPrefix('/pics/photo1.jpg')} alt="photo1" /> */}
        <div className="photo-wrapper">
          <div className="round-wrapper">
            <Img fixed={data.image1.childImageSharp.fixed} alt="photo1" />
          </div>
          <div className="str">1926年8月25日 —— 2019年</div>
          <div className="str">沉痛悼念</div>
          <div className="str">我们最亲爱的父亲，一路走好</div>
        </div>
        <br/>
        <br/>
        <br/>
        <p>　　二〇一九年Ⅹ月X日X时X分,家父因病医治无效,驾鹤西去,与世长辞,享年94岁。我们最亲爱的父亲，一路走好</p>
        {/* <img src={withPrefix('/pics/photo2.jpg')} alt="photo2" /> */}
        <p>　　父亲的一生诚实做人、本分做事，正直正派、自强不息，简朴勤劳，善良厚道。无论遇到什么困难，无论遭受多少委屈、多少困苦、多少挫折，您都默默承受，从不言苦。父亲一生问心无愧,是一个值得后辈永远追念和热爱的好父亲,我们会深深地惦念您,直到永远。</p>
        {/* <img src={withPrefix('/pics/photo3.jpg')} alt="photo3" /> */}
        <Img className="my-photo-wrapper" fixed={data.image2.childImageSharp.fixed} alt="photo2" />
        <p>　　一腔热血当年，国振民强寄深情；两袖清风和韵，理得心安享晚年。这正是父亲一生的真实写照。</p>
        <p>　　敬爱的父亲!您安息吧!我们知道，您与病魔搏斗了这些日子，现在已经很累了。愿天国再没有坎坷和病痛，祝您一路走好!如果有来生，我们还做您的儿女，您永远是我们的好父亲。</p>
        <p>　　我们最亲爱的父亲，安息吧！</p>
        <p>　　我们最亲爱的父亲，一路走好！</p>
        <p>　　衷心祝愿父亲在天国快乐、幸福！</p>
      </div>
    </Layout>
  )
}
